import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

//CSS를 미리 입혀놓은 컴포넌트를 만든다, 컴포넌트는 대문자로 시작
// class를 안쓴다. 
// 장점: 컴포넌트가 많아졌을때 class명이 중복될 수 있는데 class를 안씀으로 이를 막는다.
// 단점: styled-components는 css모듈화롤 극복이되서 선택사항이다.
let Box = styled.div`
    padding: 20px;
`;

let Title = styled.h4`
    font-size: 25px;
    color: ${ props => props.color }
`;

function Detail(props) {

    let [alert, changeAlert] = useState(true);
    let [inputData, ChangeInputData] = useState('');

    // 최신 Hook방식: 여러개 써도 되고 실행은 위에서 차례로 된다.
    useEffect( () => {
        // 컴포넌트가 mount되었을때, update될 때 실행
        // axios.get() // 페이지 이동하자마자 api요청하고 싶을때 useEffect안에 넣는다. 끝에 [] 설정을 한다.

        let timer = setTimeout( () => { changeAlert(false)}, 2000); //2초후에 alert창을 안보이게
        console.log('안녕');
        // 컴포넌트가 사라질때, 실행
        //  return function 어쩌구() { 실행할코드 } 
        return () => { clearTimeout(timer)} //타이머 해제 스킬 =>시간되기전에 뒤로가기할 수 있으니 setTimeout함수는 해제를 시켜줘야한다.
    }, [alert]); // 실행조건을 []로 맨뒤에 넣을 수 있다. alert이 '업데이트'될때만 훅발동,
    // []만 쓰면 실행시만 작동하고 업데이트시는 아무것도 작동 안함
    
    //라우터의 useParams 훅
    let { id } = useParams(); // 사용자가 입력한 URL 파라미터들이 들어있음
    let history = useHistory(); 
    let searchedItem = props.shoes.find( function(oneShoes) { return oneShoes.id == id }); 
    //filter는 조건에 맞는 모든 값을 찾아 배열로 반환, find는 조건에 맞는 첫번째 한개만 반환
    //return 뒤에서 조건식 바로 적어도됨
    return(
        <div className="container">
            <Box>
                {/* <Title color = { 'red' }>상세페이지</Title>
                <Title color = "blue" >상세페이지</Title> */}
                <Title className = "red" >상세페이지</Title>
            </Box>
            {inputData}
            <input onChange = { (e) => { ChangeInputData(e.target.value) } }/>
            {/* state변경 함수를 바로 넣으면 안되고 콜백함수 안에다가 넣어야함*/}

            {  // 리액트방식: state의 값을 조건문으로 걸고 state를 변경해서 변화를 줌
               // 항상보이는 UI가 아니면 이런식으로 만드는게 좋다.
                alert === true
                ? (<div className="my-alert2">
                    <p>재고가 얼마 남지 않았습니다.</p>
                  </div>)
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src={ 'https://codingapple1.github.io/shop/shoes' + ( searchedItem.id + 1 ) + '.jpg' } width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{ searchedItem.title }</h4>
                    <p>{searchedItem.content}</p>
                    <p>{searchedItem.price}</p> 

                    <Info stock = {props.stock}></Info>

                    <button className="btn btn-danger" onClick = { () => { 
                        props.changeStock([ [...props.stock][0] - 1, [...props.stock][1], [...props.stock][2]]) 
                    }}>주문하기</button>
                    <button className="btn btn-danger" onClick={ () => {
                        // history.goBack();
                        history.push('/');
                    }}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Info(props) { // App의 하위인 Detail의 하위인 Info
    return (
        <p>재고: {props.stock[0]}</p>
    )
}

